import { useState, useEffect } from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import Box from "@mui/material/Box";
import Sidebar from "../components/Sidebar";
import themes from "../theme";

// Global CSS imports
import "../styles/global.css"; // Your custom global styles
import "leaflet/dist/leaflet.css"; // Leaflet map styles

function MyApp({ Component, pageProps }) {
  const [themeMode, setThemeMode] = useState("light");
  const [sidebarOpen, setSidebarOpen] = useState(true);

  // Optional: Weather-based theme detection
  useEffect(() => {
    fetch("https://api.open-meteo.com/v1/forecast?latitude=28.6139&longitude=77.2090&current_weather=true")
      .then((res) => res.json())
      .then((data) => {
        const weather = data.current_weather.weathercode;
        if (weather >= 0 && weather < 3) setThemeMode("sunny");
        else if (weather >= 3 && weather < 6) setThemeMode("rainy");
        else if (weather >= 6 && weather < 8) setThemeMode("stormy");
        else setThemeMode("snowy");
      })
      .catch(() => setThemeMode("light"));
  }, []);

  const muiTheme = createTheme({
    palette: {
      mode: themeMode === "dark" ? "dark" : "light",
      background: { 
        default: themes[themeMode]?.background || "#f5f5f5",
        paper: themes[themeMode]?.paper || "#ffffff",
      },
      text: { 
        primary: themes[themeMode]?.text || "#121212",
      },
      primary: {
        main: "#1976d2",
      },
    },
    zIndex: {
      appBar: 1200,
    },
  });

  return (
    <ThemeProvider theme={muiTheme}>
      <CssBaseline />
      <Box sx={{ display: "flex" }}>
        <Sidebar 
          open={sidebarOpen}
          onToggle={() => setSidebarOpen(!sidebarOpen)}
          toggleDarkMode={() => setThemeMode(prev => prev === "dark" ? "light" : "dark")} 
          darkMode={themeMode === "dark"} 
        />
        
        {/* Main content area */}
        <Box
          component="main"
          sx={{
            flexGrow: 1,
            p: 3,
            width: `calc(100% - ${sidebarOpen ? 270 : 0}px)`,
            transition: (theme) => theme.transitions.create(['width', 'margin'], {
              easing: theme.transitions.easing.sharp,
              duration: theme.transitions.duration.leavingScreen,
            }),
            marginLeft: `${sidebarOpen ? 270 : 0}px`,
            display: "flex",
            flexDirection: "column",
            alignItems: "center", // Center content horizontally
          }}
        >
          {/* Centered container for all pages */}
          <Box sx={{ 
            width: "100%",
            maxWidth: "1200px", // Maximum content width
            display: "flex",
            flexDirection: "column",
            alignItems: "center" // Ensures dashboard stays centered
          }}>
            <Component {...pageProps} />
          </Box>
        </Box>
      </Box>
    </ThemeProvider>
  );
}

export default MyApp;